














































import {TableToolbar,SearchBar} from "@/components"
import EditDialog from "../dialog/EditDialog.vue"

import { State, Action, namespace } from "vuex-class"
const articleModule = namespace("article")
import { Component, Vue ,Watch} from "vue-property-decorator"
interface PaginationValue {
  total?: number;
  pagesize: number;
  pagesizes: number[];
  currentPage: number;
}
@Component({
  components: {
    EditDialog,
    TableToolbar,
    SearchBar
  }
})
export default class Article extends Vue {
  @articleModule.State("articleList") articleList;
  @articleModule.State("articleTotal") articleTotal;
  @articleModule.Action("delArticleAction") delArticleAction;
  @articleModule.Action("getListAction") getListAction;
  isShowEditDialog: boolean = false;
  articleEditData: object = {};
  tableData: any[] = [];
  pagination: PaginationValue = {
    pagesize: 10,
    pagesizes: [10, 20, 30, 40],
    currentPage: 1
  };
  searchConditions: object = {
    author: "",
    cate: "",
    postTime: []
  };
  value: string = "ss";
  options: any[] = [{ value: 1, label: "hansu" }, { value: 2, label: "lilei" }];
  loading:boolean=true

  mounted() {
    this.getList();
  }

  async getList(r?: string) {
    let {pagesize,currentPage}=this.pagination
    let params = Object.assign({pagesize,currentPage},(this as any).$refs.searchBar.formInline);
    let d = await this.getListAction(params);
    if (d.list.length > 0) {
      this.loading=false
      this.tableData = this.articleList;
    }

    if (r === "refresh") {
      (this as any).$message({
        type: "success",
        message: "已是最新数据"
      });
    }
  }
  handleDialog() {
    this.isShowEditDialog = false;
  }
  handleEdit(data) {
    this.articleEditData = data;
    this.isShowEditDialog = true;
  }

  async handleDelete(data) {
    let res = await (this as any).$confirm(
      "此操作将永久删除文章, 是否继续?",
      "提示",
      {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }
    );
    try {
      if (res) {
        await this.delArticleAction(data._id);
      }
    } catch (err) {
      (this as any).$message({
        type: "info",
        message: "已取消删除"
      });
    }
  }
  handleSizeChange(val) {
    this.pagination.pagesize = val;
  }
  handleCurrentChange(val) {
    this.pagination.currentPage = val;
    this.getList()
  }
  search() {
    this.getList();
  }

  handleClick(row) {}

  handleChange() {
    alert(this.value);
  }

  @Watch("articleList")
  articleListChange(newVal){
    this.tableData=newVal
  }

  
}

