










































import {Component,Vue,Prop,Watch} from "vue-property-decorator"
@Component({
  components:{

  }
})
export default class EditDialog extends Vue{
  @Prop(Boolean)
  outerVisible!:boolean
  @Prop(Object)
  ruleForm!:any
  rules={
    title: [
      { required: true, message: "请输入文章标题", trigger: "blur" },
      { min: 3, max: 20, message: "长度在 3 到 20 个字符", trigger: "blur" }
    ],
    cate: [{ required: true, message: "请选择分类", trigger: "change" }],
    summary: [
      { required: true, message: "请填写文章简介", trigger: "blur" }
    ]
  }

  handleStatus() {
    this.outerVisible = false;
    this.$emit("changeDialog");
  }

  submitForm(formName) {
    (this as any).$refs[formName].validate(async(valid:boolean) => {
      if (valid) {
        let { _id, title, cate, summary } = this.ruleForm;
        let data = { _id, title, cate, summary };
        let d=await (this as any).$API.articleModel.EditArticle(data)
        if(d.code===0){
          this.$message({
            message: "修改成功",
            type: "success",
            onClose:()=>{
              this.handleStatus()
            }
          });
        }
        else{
           this.$message({
            message: "修改失败",
            type: "error",
            onClose:()=>{
              this.handleStatus()
            }
          });
        }
      }
      });
  }

  @Watch("outerVisible")
  outerVisibleChange(newVal,oldVal){
    !newVal&&this.handleStatus();
  }
}

